import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCalendarAlt, faUser } from "@fortawesome/free-solid-svg-icons";

const BlogContent = () => {
  const { id } = useParams(); // الحصول على ID المدونة من URL
  const [blog, setBlog] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchBlog = async () => {
      try {
        // إرسال استعلام إلى الخادم باستخدام ID المدونة
        const response = await fetch(`http://localhost:8080/api/blogs/${id}`);

        if (!response.ok) {
          throw new Error("Failed to fetch blog");
        }

        const data = await response.json();
        setBlog(data); // تعيين المدونة المسترجعة في الحالة
        setLoading(false);
      } catch (error) {
        console.error("Error fetching blog:", error);
        setLoading(false);
      }
    };

    fetchBlog(); // استدعاء الوظيفة لجلب المدونة
  }, [id]);

  if (loading) {
    return (
      <div className="text-center my-5">
        <div className="spinner-border" role="status">
          <span className="sr-only">Loading...</span>
        </div>
      </div>
    );
  }

  if (!blog) {
    return <div className="text-center my-5">Blog not found</div>;
  }

  return (
    <section className="blog-content-section py-5">
      <div className="container">
        <div className="card shadow-lg border-0">
          <div className="card-body">
            <h2 className="text-center mb-4">{blog.title}</h2>
            <div className="row">
              <div>
                {/* عرض الصورة */}
                <img
                  // تعديل هنا لعرض الصورة من المسار الصحيح
                  src={`http://localhost:8080/assets/imageBlogs/${blog.imagePath || "default-image.jpg"}`}
                  alt={blog.title || "Blog image"}
                  className="img-fluid rounded mb-4 w-100"
                  style={{ maxHeight: "400px", objectFit: "cover" }}
                  loading="lazy"
                />
                {/* عرض التاريخ والمؤلف */}
                <div className="d-flex justify-content-between mb-4 text-muted">
                  <span>
                    <FontAwesomeIcon icon={faCalendarAlt} />{" "}
                    {new Date(blog.createdAt).toLocaleDateString()}
                  </span>
                  <span>
                    <FontAwesomeIcon icon={faUser} />{" "}
                    {blog.author || "Unknown Author"}
                  </span>
                </div>
                {/* عرض المحتوى بتنسيق HTML */}
                <div
                  className="blog-content"
                  dangerouslySetInnerHTML={{ __html: blog.content }}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default BlogContent;
